import React, {Component} from 'react';
import {Button, Col, Table, DatePicker, Input, Form, Modal} from "antd";
import {deleteNews, getNewsList} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import moment from 'moment';
import {DeleteOutlined, EditOutlined, PlusOutlined, ReloadOutlined, SearchOutlined} from "@ant-design/icons";
import {Link} from "react-router-dom";
import DocumentTitle from 'react-document-title'
import {disabledDate} from "../../../utils/var";
/*
 * list.jsx
 * liunengkai
 * 2019-07-24 - 22:28
 * 
 */
const {RangePicker} = DatePicker;
// ES6
class List extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            beginTime: null,// 
            endTime: null,// 
            topic: null // 
        },
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'source', // 
            },
            {
                title: '',
                dataIndex: 'topic', // 
            },
            {
                title: '',
                dataIndex: 'createtime', // 
            },
            {
                title: '',
                dataIndex: 'updatetime', // 
            },
            {
                title: '',
                render: (text, record) => (
                    <div>
                        <Button type="primary" onClick={() => this.props.history.push('/backstage/memory/news/update', record)} shape="circle" icon={<EditOutlined/>}/>
                        &nbsp;
                        <Button type="danger" onClick={() => this.handleDellNews(record)}  shape="circle" icon={<DeleteOutlined/>}/>
                    </div>
                ),
            },
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            topic: this.state.filters.topic,
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getNewsList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /*
    * 
    */
    handleDellNews = (item) => {
        let _this = this;
        Modal.confirm({
            title: '',
            content: `:${item.topic}?`,
            onOk: async () => {
                // , loading
                _this.setState({listLoading: true});
                let para = { id: item.id };
                const {msg, code} = await deleteNews(para);
                // , loading
                _this.setState({listLoading: false});
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.getDatas();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            }
        })
    };

    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters= _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.topic = null;
        _this.setState({
            nowPage: 1,
            filters: filters,
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        // reactevent handlersetState, state.
        this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        this.setState({
            nowPage: current,
        }, function () {
            this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== ''){
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        }else{
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nowPage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };


    /**
     * 
     * @param event
     */
    topicInputChange = (event) => {
        let _this = this;
        const value = event.target.value;
        let filters = _this.state.filters;
        filters.topic = value;
        _this.setState({
            nowPage: 1,
            filters
        })
    };

    /*
    *render()
    * async
    */
    componentWillMount() {
        // 
        this.initColumns();
    };

    /*
    : ajax
     */
    componentDidMount() {
        // 
        this.getDatas();
    };


    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters} = this.state;
        let {beginTime,endTime,topic} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null){
            rangeDate = [moment(beginTime),moment(endTime)]
        } else {
            rangeDate = [null,null]
        }
        return (
            <DocumentTitle title=''>
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Input type='text' value={topic} onChange={this.topicInputChange}
                                       placeholder=''/>
                            </Form.Item>
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                    <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                    <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button">
                                    <Link to='/backstage/memory/news/publish'><PlusOutlined/></Link>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="id" bordered loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default List;
