import React, {Component} from 'react';
import {getTransactionList, getFinancialType, deleteTransaction, downTransaction, outTransactionInfoExcel} from '../../../api'
import {
  SearchOutlined,
  ReloadOutlined,
  FileExcelOutlined,
  PlusOutlined,
  EditOutlined,
  EyeOutlined,
  DeleteOutlined} from '@ant-design/icons';
import DocumentTitle from 'react-document-title'
import moment from 'moment';
import {Button, Col, DatePicker, Form, Select, Table, Modal} from "antd";
import {openNotificationWithIcon,showLoading} from "../../../utils/window";
import BillDeclare from './declare'
import BillDetail from './detail'
import BillRenew from './renew'
import axios from "axios";
import {formatMoney,disabledDate} from '../../../utils/var'
/*
 * transaction.jsx
 * liunengkai
 * 2019-08-27 - 21:46
 * 
 */
const {RangePicker} = DatePicker;
const {Option} = Select;

// ES6
class Transaction extends Component {

    billDetailRef = React.createRef();
    billDeclareRef = React.createRef();
    billRenewRef = React.createRef();


    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            beginTime: null,// 
            endTime: null,// 
            tradeType: ''//
        },
        queryType: [],// 
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'tradeId', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'deposited', // 
                align:'right',
                render:(value,row) => (formatMoney(row.deposited))
            },
            {
                title: '',
                dataIndex: 'expenditure',// 
                align:'right',
                render:(value,row) => (formatMoney(row.expenditure))
            },
            {
                title: '',
                dataIndex: 'tradeTypeEntity.transactionType',// 
                align:'center',
                render: (value, row) => (
                  <span>{!row.tradeTypeEntity?'-':row.tradeTypeEntity.transactionType}</span>
                ),
            },
            {
                title: '',
                dataIndex: 'tradeAmountEntity.tag',// 
                render: (value, row) => (
                  <span>{!row.tradeAmountEntity ? '-' : row.tradeAmountEntity.tag}</span>
                ),
            },
            {
                title: '',
                dataIndex: 'tradeDate', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'currencyNumber',// 
                align:'right',
                render:(value,row) => (formatMoney(row.currencyNumber))
            },
            {
                title: '',
                dataIndex: 'updateTime', // 
                align:'center',
            },
            {
                title: '',
                align:'center',
                render: (value, row) => (
                  <div>
                    <Button type="primary" onClick={() => this.openViewModal(row)} shape="circle"
                            icon={<EyeOutlined/>}/>
                    &nbsp;
                    <Button type="primary" onClick={() => this.openEditModal(row)} shape="circle"
                            icon={<EditOutlined/>}/>
                    &nbsp;
                    <Button type="danger" onClick={() => this.handleDelete(row)} shape="circle"
                            icon={<DeleteOutlined/>}/>
                  </div>
                ),
            },
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            tradeType: this.state.filters.tradeType,
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        // , loading
        this.setState({listLoading: showLoading()});
        // ajax, 
        const {msg, code, data} = await getTransactionList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.tradeType = null;
        _this.setState({
            nowPage: 1,
            filters: filters,
        }, function () {
            _this.getDatas()
        });
    };


    // ,
    changePageSize = (pageSize, current) => {
        let _this = this;
        // reactevent handlersetState, state.
        _this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            _this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        let _this = this;
        _this.setState({
            nowPage: current,
        }, function () {
            _this.getDatas();
        });
    };

    /**
     * 
     */
    initDatas = async () => {
        let _this = this;
        // ajax, 
        const {msg, code, data} = await getFinancialType();
        if (code === 0) {
            let type = [];
            data.forEach(item => {
                type.push((<Option key={item.id} value={item.id}>{item.transactionType}</Option>));
            });
            let copyType = [];
            copyType.push(<Option key='-1' value=""></Option>);
            copyType.push(type);
            _this.setState({
                queryType: copyType
            })
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== '') {
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        } else {
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            filters,
            nowPage: 1,
        }, function () {
            _this.getDatas()
        });
    };

    // 
    onChangeType = (value) => {
        let _this = this;
        let filters = _this.state.filters;
        filters.tradeType = value;
        _this.setState({
            filters,
            nowPage: 1,
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * 
     */
    handleAddModal = () => {
        const _this = this;
        // 
        _this.billDeclareRef.handleDisplay()
    };

    /**
     * 
     */
    openViewModal = (value) => {
        const _this = this;
        // 
        _this.billDetailRef.handleDisplay(value.tradeId)
    };

    /**
     * 
     * @param value
     */
    openEditModal = (value) => {
        const _this = this;
        // 
        _this.billRenewRef.handleDisplay(value.tradeId);
    };

    bindBillDetailRef = (ref) => {
        this.billDetailRef = ref
    };

    bindBillDeclareRef = (ref) => {
        this.billDeclareRef = ref
    };

    bindBillRenewRef = (ref) => {
        this.billRenewRef = ref
    };

    /**
     * 
     */
    handleDelete = (item) => {
        let _this = this;
        Modal.confirm({
            title: '',
            content: `${item.tradeId}?`,
            onOk: async () => {
                // , loading
                _this.setState({listLoading: true});
                let para = {tradeId: item.tradeId};
                const {msg, code} = await deleteTransaction(para);
                // , loading
                _this.setState({listLoading: false});
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.getDatas();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            }
        })
    };

    /**
     * 
     */
    exportListExcel = () => {
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        let para = {
            tradeType: this.state.filters.tradeType,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        console.log(para);
        axios({
            method: "GET",
            url: downTransaction,   //
            params: para,           //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = '.xlsx';//excel
                let blob = new Blob([res.data], {type: 'application/x-xlsx'});   //wordmsword,pdfpdfexcelx-xls
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                console.log(res);
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", "");
            });
    };

    /**
     * 
     */
    exportInfoExcel = () => {
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        let para = {
            tradeType: this.state.filters.tradeType,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        axios({
            method: "GET",
            url: outTransactionInfoExcel,   //
            params: para,           //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = '.xlsx';//excel
                let blob = new Blob([res.data], {type: 'application/x-xlsx'});   //wordmsword,pdfpdfexcelx-xls
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                console.log(res);
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", "");
            });
    };

    /**
     * 
     */
    refreshListFromDeclare = () =>{
        this.getDatas();
    };

    /**
     * 
     */
    refreshListFromRenew = () =>{
        this.getDatas();
    };

    /**
     * 
     */
    componentDidMount() {
      // 
      this.refreshListFromDeclare  = this.refreshListFromDeclare.bind(this);
      this.refreshListFromRenew  = this.refreshListFromRenew.bind(this);
      // 
      this.initColumns();
      this.initDatas();
      // 
      this.getDatas();
    };

    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading, queryType, filters} = this.state;
        let {beginTime, endTime, tradeType} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null) {
            rangeDate = [moment(beginTime), moment(endTime)]
        } else {
            rangeDate = [null, null]
        }
        return (
            <DocumentTitle title="">
                <section>
                    <BillDeclare onRef={this.bindBillDeclareRef.bind(this)} refreshList={this.refreshListFromDeclare}/>
                    <BillDetail onRef={this.bindBillDetailRef.bind(this)}/>
                    <BillRenew onRef={this.bindBillRenewRef.bind(this)} refreshList={this.refreshListFromRenew}/>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Select style={{width: '200px'}} value={tradeType} showSearch
                                        onChange={this.onChangeType} placeholder="">
                                    {queryType}
                                </Select>
                            </Form.Item>
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                    <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                    <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.handleAddModal}>
                                    <PlusOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.exportListExcel}>
                                    <FileExcelOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.exportInfoExcel}>
                                    <FileExcelOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="tradeId" bordered loading={listLoading} columns={this.columns}
                               dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize,
                                   showQuickJumper: true,
                                   total: dataTotal,
                                   showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default Transaction;
