import React, {Component} from 'react';
import Editor from 'for-editor'
import axios from "axios";
import {uploadNewsPicture} from "../api";
import {openNotificationWithIcon} from "../utils/window";
import {isEmptyObject} from "../utils/var"
/*
 * for-editor.jsx
 * liunengkai
 * 2019-10-27 - 17:37
 * for-editormarkdown
 */

// ES6
class ForEditor extends Component {

    editorRef = React.createRef();

    state = {
      value: '', // 
    };

    /**
     * Markdown
     * @param val
     */
    initEditor = (val) => {
      const _this = this;
      _this.setState({
        value: val
      });
    };

    //
    handleChange = (value) => {
        this.setState({
            value
        })
    };

    //
    handleAddImg = async (file) => {
        const {data} = await this.addImg(file);
        if (!isEmptyObject(data)) {
            // url
            this.editorRef.current.$img2Url(file.name, data.link)
        } else {
            this.editorRef.current.$img2Url(file.name, 'file_url')
        }
    };

    //
    addImg = (file) => {
        return new Promise(
            (resolve, reject) => {
                let img = new Image();
                let reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function (e) {
                    img.src = this.result;
                };
                img.onload = function () {
                    let para = {
                        filename: file.name,
                        fileurl: img.src
                    };
                    axios.post(uploadNewsPicture, para).then(response => {
                        const data = response.data; // 
                        if (data.code === 0) {
                            resolve({data: {link: data.data}})
                        } else {
                            openNotificationWithIcon("error", "", data.msg);
                            resolve({data: {link: ''}})
                        }
                        // /files/picture/illustrated/Pandora/20190728/2019072823539.png
                    }).catch(error => {
                        openNotificationWithIcon("error", "", error.message);
                        reject(error)
                    })
                }
            })
    };

    /**
     * 
     * @returns {boolean}
     */
    isNull = () => {
        return null === this.state.value || "" === (this.state.value).trim();
    };

    /**
     * markdown
     * @returns {*}
     */
    getValue = () => {
        return this.state.value;
    };

    /**
     * : ajax
     */
    componentDidMount() {
      // 
      const _this = this;
      _this.props.onRef(_this);
    };

    render() {
        const {value} = this.state;
        return (
            <Editor
                ref={this.editorRef}
                value={value}
                lineNum={false}
                style={{border:'none',boxShadow:'none',maxWidth:'80em'}}
                addImg={(file) => this.handleAddImg(file)}
                onChange={value => this.handleChange(value)}
            />
        );
    }
}

// 
export default ForEditor;
