import React, {Component} from 'react';
import './index.less'
import {Button,message, Input} from 'antd';
import DocumentTitle from 'react-document-title'
import {requestLogin} from '../../api'
import memoryUtils from '../../utils/memoryUtils'
import storageUtils from '../../utils/storageUtils'
/*
 * index.jsx
 * 
 * 2020-7-15 - 14:22
 * 
 */

const activeLabel = {
  lineHeight: "18px",
  fontSize: "18px",
  fontWeight: 100,
  top: "0px"
};

const activeSpin = {
  width: "100%"
};

const unactiveLabel = {
  lineHeight: "60px",
  fontSize: "24px",
  fontWeight: 300,
  top: "10px"
};

const unactiveSpin = {
  width: "0px"
};

// ES6
class Login extends Component {

  state = {
    // 
    userState: false,
    // 
    pwdState: false,
    // 
    userName: '',
    passWord: '',
    loading: false
  };


  /**
   * 
   */
  userOnBlur = () => {
    let _this = this;
    let {userName} = _this.state;
    // 
    if (null === userName || "" === userName) {
      _this.setState({userState: false});
    } else {
      _this.setState({userState: true});
    }
  };

  /**
   * 
   */
  userOnFocus = () => {
    let _this = this;
    _this.setState({userState: true});
  };

  /**
   * 
   */
  pwdOnBlur = () => {
    let _this = this;
    let {passWord} = _this.state;
    // 
    if (null === passWord || "" === passWord) {
      _this.setState({pwdState: false});
    } else {
      _this.setState({pwdState: true});
    }
  };

  /**
   * 
   */
  pwdOnFocus = () => {
    let _this = this;
    _this.setState({pwdState: true});
  };

  /**
   * 
   * @param event
   */
  userInputChange = (event) => {
    let _this = this;
    const value = event.target.value;
    _this.setState({
      userName: value.trim()
    })
  };

  /**
   * 
   * @param event
   */
  pwdInputChange = (event) => {
    let _this = this;
    const value = event.target.value;
    _this.setState({
      passWord: value.trim()
    })
  };

  /**
   * 
   */
  loginHandle = async () => {
    let _this = this;
    let {userName,passWord} = _this.state;
    if (null === userName || null === passWord || '' === userName || '' === passWord){
      message.error('');
      return
    }
    let loginParams = {user: userName, password: passWord};
    _this.setState({loading: true});
    const result = await requestLogin(loginParams);
    let {code, data} = result;
    _this.setState({loading: false});
    if (code === 0) {
      memoryUtils.user = data;// 
      storageUtils.saveUser(data); // local
      //  (),push
      this.props.history.replace('/backstage/chart')
    } else if (code === 5) {
      message.error('');
    } else {
      message.error('');
    }
  };

  render() {
    // 
    const {userState, pwdState, userName, passWord,loading} = this.state;
    return (
      <DocumentTitle title=''>
        <div className="login-register-container"
             style={{backgroundImage: `url('${process.env.PUBLIC_URL}/picture/login/login_background1.png')`}}>
          <div className="login-register-box">
            <div className="login-box">
              <div className="title"></div>
              <div className="input">
                <label style={userState ? activeLabel : unactiveLabel}></label>
                <Input type="text" value={userName} onChange={this.userInputChange} onBlur={this.userOnBlur}
                       onFocus={this.userOnFocus}/>
                <span className="spin" style={userState ? activeSpin : unactiveSpin}></span>
              </div>
              <div className="input">
                <label style={pwdState ? activeLabel : unactiveLabel}></label>
                <Input type="password" value={passWord} onChange={this.pwdInputChange} onBlur={this.pwdOnBlur}
                       onFocus={this.pwdOnFocus}/>
                <span className="spin" style={pwdState ? activeSpin : unactiveSpin}></span>
              </div>
              <div className="button login">
                <Button type="text" onClick={this.loginHandle} loading={loading}></Button>
              </div>
              <Button type="link" className="pass-forgot">
                
              </Button>
            </div>
          </div>
        </div>
      </DocumentTitle>
    );
  }
}

export default Login;
