import React, { Component } from 'react';
import {Col, Tabs, Card, Form, Input, Button, Spin} from 'antd';
import {getPersonal, setPassword, setUserInfo} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import {clearTrimValueEvent} from "../../../utils/string"
import './index.less'
import UploadLogo from './uploadLogo'
import DocumentTitle from 'react-document-title'
const {TabPane} = Tabs;
/*
 * index.jsx
 * liunengkai
 * 2019-07-14 - 17:30
 * 
 */

// ES6
class Info extends Component {

  pwdFormRef = React.createRef();

  autographFormRef = React.createRef();

  state = {
      // 
      personalInfo: {},
      // 
      loading: false,
  };

    /**
     * 
     * @returns {Promise<void>}
     */
    getPersonalInfo = async () => {
        let _this = this;
        // , loading
        _this.setState({loading: true});
        // ajax, 
        const {msg, code, data} = await getPersonal();
        // , loading
        _this.setState({loading: false});
        if (code === 0) {
            _this.setState({
                personalInfo: data
            })
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /*
    *
    */
    /*
      /
      1). 
      2). 6
      3). 20
      4). 
    */
    validatePwd1 = (rule, value, callback) => {
        // console.log(this.props.form.getFieldValue('password'));
        // this.props.form.setFieldsValue({
        //     password:1111111
        // })
        if (!value) {
          return callback('')
        } else if (value.length < 6) {
          return callback('6')
        } else if (value.length > 20) {
          return callback('20')
        } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
          return callback('')
        } else {
          return callback() // 
        }
        // callback('xxxx') // , 
    };

    validatePwd2 = (rule, value,callback) => {
        // 
        const password = this.pwdFormRef.current.getFieldValue('password1');
        if (!value) {
          return callback('')
        } else if (value.length < 6) {
          return callback('6')
        } else if (value.length > 20) {
          return callback('20')
        } else if (!/^[a-zA-Z0-9_]+$/.test(value)) {
          return callback('')
        } else if (value !== password) {
          return callback('')
        } else {
          return callback() // 
        }
    };

    // 
    handlePwdSubmit = (e) => {
      let _this = this;
      // 
      //e.preventDefault();
      _this.pwdFormRef.current.validateFields(['password1', 'password2']).then(async value => {
          // , loading
          _this.setState({loading: true});
          let para = {
              password: value.password2
          };
          const result = await setPassword(para);
          // , loading
          _this.setState({loading: false});
          let {msg, code} = result;
          if (code === 0) {
              openNotificationWithIcon("success", "", "");
          } else {
              openNotificationWithIcon("error", "", msg);
          }
      }).catch(e => console.log("",e));
    };

    // 
    handleAutographSubmit = (e) => {
        let _this = this;
        // 
        // e.preventDefault();
        _this.autographFormRef.current.validateFields(['autograph']).then(async value => {
          // , loading
          _this.setState({loading: true});
          let para = {
            autograph: value.autograph
          };
          const result = await setUserInfo(para);
          // , loading
          _this.setState({loading: false});
          let {msg, code} = result;
          if (code === 0) {
            openNotificationWithIcon("success", "", "");
          } else {
            openNotificationWithIcon("error", "", msg);
          }
        }).catch(e => console.log("",e));
    };

    /**
     * 
     */
    componentDidMount() {
        this.formItemLayout = {
            labelCol: {span: 4},
            wrapperCol: {span: 14},
        };
        this.buttonItemLayout = {
            wrapperCol: {span: 14, offset: 4},
        };
        this.getPersonalInfo()
    }


    render() {
        // 
        const {personalInfo, loading} = this.state;
        return (
            <DocumentTitle title=''>
                <section className="b-info-page">
                    <div className="mytips">
                        <p></p>
                        <blockquote>
                            <p>
                                <i>1</i>
                            </p>
                            <p>
                                <i>2140</i>
                            </p>
                            <p>
                                <i>35M</i>
                            </p>
                            <p>
                                <i>4620</i>
                            </p>
                        </blockquote>
                    </div>
                    <Col span={24} className="b-info-page-form">
                        <Spin spinning={loading}>
                            <Tabs defaultActiveKey="1">
                                <TabPane tab="" key="1">
                                    <Card title="" bordered={false} className="bk-transparent">
                                        <Form {...this.formItemLayout}>
                                            <Form.Item label="" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.user} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.sex} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.email} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="qq" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.qq} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="phone" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.phone} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.birthday} disabled={true}/>
                                            </Form.Item>
                                            <Form.Item label="" {...this.formItemLayout}>
                                                <Input type='text' value={personalInfo.hometown} disabled={true}/>
                                            </Form.Item>
                                        </Form>
                                    </Card>
                                </TabPane>
                                <TabPane tab="" key="2">
                                    <Card title="" bordered={false} className="bk-transparent">
                                        <Form {...this.formItemLayout} ref={this.autographFormRef} onFinish={this.handleAutographSubmit}>
                                            <Form.Item label="" {...this.formItemLayout} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='autograph' initialValue={personalInfo.autograph} rules={[{required: true, message: ''},{min: 1, message: ' 1  140 '},{max: 140, message: ' 1  140 '}]}>
                                              <Input.TextArea showCount maxLength={140} autosize={{minRows: 4, maxRows: 6}}/>
                                            </Form.Item>
                                            <Form.Item {...this.buttonItemLayout}>
                                                <Button type="primary" htmlType="submit"></Button>
                                            </Form.Item>
                                        </Form>
                                    </Card>
                                </TabPane>
                                <TabPane tab="" key="3">
                                    <Card title="" bordered={false} className="bk-transparent">
                                        <UploadLogo/>
                                    </Card>
                                </TabPane>
                                <TabPane tab="" key="4">
                                    <Card title="" bordered={false} className="bk-transparent">
                                        <Form {...this.formItemLayout} ref={this.pwdFormRef} onFinish={this.handlePwdSubmit}>
                                            <Form.Item label="" {...this.formItemLayout} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='password1' rules={[{validator: this.validatePwd1}]}>
                                              <Input.Password/>
                                            </Form.Item>
                                            <Form.Item label="" {...this.formItemLayout} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='password2' rules={[{validator: this.validatePwd2}]}>
                                              <Input.Password/>
                                            </Form.Item>
                                            <Form.Item {...this.buttonItemLayout}>
                                                <Button type="primary" htmlType="submit"></Button>
                                            </Form.Item>
                                        </Form>
                                    </Card>
                                </TabPane>
                            </Tabs>
                        </Spin>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

export default Info
