import React, {Component} from 'react';
import {Button, Col, DatePicker, Input, Table, Form, Modal, Tag, Select} from "antd";
import {getNotesList, deleteNotes, getNoteBookGroup} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import {Link} from "react-router-dom";
import DocumentTitle from 'react-document-title'
import moment from 'moment';
import {DeleteOutlined, EditOutlined, PlusOutlined, ReloadOutlined, SearchOutlined} from "@ant-design/icons";
import {disabledDate} from "../../../utils/var"
import {getUrlParameter} from "../../../utils/url"
/*
 * list.jsx
 * liunengkai
 * 2019-08-26 - 21:09
 * 
 * 
 */
const {RangePicker} = DatePicker;
const {Option} = Select;
// ES6
class NotesList extends Component {

    constructor(props) {
        super(props);
        let _this = this
        // 
        const search = getUrlParameter ('search',props.location.search);
        let state = {
          // 
          datas: [],
          // 
          dataTotal: 0,
          // 
          nowPage: 1,
          // 
          pageSize: 10,
          // 
          listLoading: false,
          tagColor:['magenta','red','volcano','orange','gold','lime','green','cyan','blue','geekblue','purple'],
          notebooks: [],
        };
        if (!!search){
          state.filters = {
            beginTime: null,// 
            endTime: null,// 
            topic: search, // 
            notebookId: null
          }
        }else{
          state.filters = {
            beginTime: null,// 
            endTime: null,// 
            topic: null, // 
            notebookId: null
          }
        }
      _this.state = state
    }


    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                render: (value, row) => (
                  <span>{!row.notebook?'-':row.notebook.source}</span>
                ),
            },
            {
                title: '',
                render: (value, row) => (
                  <span>{!row.notebook?'-':row.notebook.name}</span>
                ),
            },
            {
                title: '',
                dataIndex: 'topic', // 
            },
            {
              title: '',
              render: (value, row) => {
                const tags = row.label === null ? [] : (row.label).split(';')
                if (tags.length > 0){
                  return tags.map(this.forTagMap)
                }else{
                  return ''
                }
              },
            },
            {
                title: '',
                dataIndex: 'createtime', // 
            },
            {
                title: '',
                dataIndex: 'updatetime', // 
            },
            {
                title: '',
                render: (text, record) => (
                    <div>
                        <Button type="primary" onClick={() => this.props.history.push('/backstage/memory/notes/update', record)} shape="circle" icon={<EditOutlined/>}/>
                        &nbsp;
                        <Button type="danger"  shape="circle" onClick={() => this.handleDeleteNotes(record)} icon={<DeleteOutlined/>}/>
                    </div>
                ),
            },
        ]
    };

  forTagMap = tag => {
    let colors = this.state.tagColor;
    const tagElem = (
      <Tag color={colors[Math.floor(Math.random()*10)]}>
        {tag}
      </Tag>
    );
    return (<span key={tag} style={{ display: 'inline-block' }}>{tagElem}</span>
    );
  };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            'nowPage': this.state.nowPage,
            'topic': this.state.filters.topic,
            'notebookId':this.state.filters.notebookId,
            'beginTime': this.state.filters.beginTime,
            'endTime': this.state.filters.endTime,
            'pageSize': this.state.pageSize,
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getNotesList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

  /**
   * 
   */
  getNoteBooks = async () =>{
    let _this = this;
    // ajax, 
    const {msg, code, data} = await getNoteBookGroup()
    if (code === 0) {
      let notebooks = [];
      notebooks.push(<Option key='-1' value=""></Option>);
      data.forEach(item => {
        notebooks.push((<Option key={item.id} value={item.id}>{item.name}</Option>));
      });
      _this.setState({
        notebooks
      })
    } else {
      openNotificationWithIcon("error", "", msg);
    }
  }


    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.topic = null;
        filters.name = null;
        _this.setState({
            nowPage: 1,
            filters: filters,
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        // reactevent handlersetState, state.
        this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        this.setState({
            nowPage: current,
        }, function () {
            this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== ''){
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        }else{
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nowPage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };


    /**
     * 
     * @param event
     */
    topicInputChange = (event) => {
        let _this = this;
        const value = event.target.value;
        let filters = _this.state.filters;
        filters.topic = value;
        _this.setState({
            nowPage: 1,
            filters
        })
    };

  // 
  onChangeBooks = (value) => {
    let _this = this;
    let filters = _this.state.filters;
    filters.notebookId = value;
    _this.setState({
      filters,
      nowPage: 1,
    }, function () {
      _this.getDatas()
    });
  };

    /*
    * 
    */
    handleDeleteNotes = (item) => {
        let _this = this;
        Modal.confirm({
            title: '',
            content: `:${item.topic}?`,
            onOk: async () => {
                // , loading
                _this.setState({listLoading: true});
                let para = { id: item.id };
                const {msg, code} = await deleteNotes(para);
                // , loading
                _this.setState({listLoading: false});
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.getDatas();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            }
        })
    };

    /*
    *render()
    * async
    */
    componentDidMount() {
        // 
        this.initColumns();
        // 
        this.getDatas();
        // 
        this.getNoteBooks()
    };

    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters,notebooks} = this.state;
        let {beginTime,endTime,topic,notebookId} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null){
            rangeDate = [moment(beginTime),moment(endTime)]
        } else {
            rangeDate = [null,null]
        }
        return (
            <DocumentTitle title="">
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Input type='text' value={topic} onChange={this.topicInputChange}
                                       placeholder=''/>
                            </Form.Item>
                            <Form.Item label=":">
                              <Select showSearch placeholder="" style={{width: '200px'}} value={notebookId} onChange={this.onChangeBooks}>
                                {notebooks}
                              </Select>
                            </Form.Item>
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                  <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                  <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button">
                                    <Link to='/backstage/memory/notes/create'><PlusOutlined/></Link>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="id" loading={listLoading} bordered columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default NotesList;
