import React, {Component} from 'react';
import {totalTransactionForYear, outTransactionForYearExcel} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import axios from 'axios'
import DocumentTitle from 'react-document-title'
import {Button, Col, Icon, Table, Form} from "antd";
import {formatMoney} from "../../../utils/var";
import {FileExcelOutlined} from "@ant-design/icons";
/*
 * index.jsx
 * liunengkai
 * 2019-09-01 - 15:09
 * 
 */
// ES6
class FinancialForYear extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'tradeDate', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'deposited', // 
                align:'right',
                render:(value,row) => (formatMoney(row.deposited))
            },
            {
                title: '',
                dataIndex: 'expenditure', // 
                align:'right',
                render:(value,row) => (formatMoney(row.expenditure))
            },
            {
                title: '',
                dataIndex: 'currencyNumber', // 
                align:'right',
                render:(value,row) => (formatMoney(row.currencyNumber))
            }
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let _this = this;
        let para = {
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
        };
        // , loading
        _this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await totalTransactionForYear(para);
        // , loading
        _this.setState({listLoading: false});
        if (code === 0) {
            _this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };


    // ,
    changePageSize = (pageSize, current) => {
        let _this = this;
        // reactevent handlersetState, state.
        _this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            _this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        let _this = this;
        _this.setState({
            nowPage: current,
        }, function () {
            _this.getDatas();
        });
    };

    /**
     * 
     */
    exportExcel = () =>{
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        axios({
            method: "GET",
            url: outTransactionForYearExcel,   //
            //params: para,           //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = '.xlsx';//excel
                let blob = new Blob([res.data], {type: 'application/x-xlsx'});   //wordmsword,pdfpdfexcelx-xls
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                console.log(res);
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", "");
            });
    };

    /*
    *render()
    * async
    */
    componentDidMount() {
        // 
        this.initColumns();
        // 
        this.getDatas();
    };


    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading} = this.state;
        return (
            <DocumentTitle title="">
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.exportExcel}>
                                    <FileExcelOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" bordered rowKey='tradeDate' loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default FinancialForYear;