import React, {Component} from 'react';
import {Button, Col, DatePicker, Icon, Table, Form, Input, Modal, Upload} from "antd";
import {getFileList, deleteFile, editFile, uploadFile, downloadFileForAdmin} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import moment from 'moment';
import axios from "axios";
import DocumentTitle from 'react-document-title'
import {DeleteOutlined, CloudDownloadOutlined, EditOutlined, ReloadOutlined, SearchOutlined,CloudUploadOutlined} from "@ant-design/icons";
import {disabledDate} from "../../../utils/var";
/*
 * index.jsx
 * liunengkai
 * 2019-08-11 - 17:31
 * 
 */
const {RangePicker} = DatePicker;
const { Dragger } = Upload;
// ES6
class Files extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        //
        uploadVisible: false,
        filters: {
            beginTime: null,// 
            endTime: null,// 
            filename: null
        },
    };

    /**
     * 
     */
    initUpload = () => {
        let _this = this;
        _this.uploadConfig = {
            name: 'file',
            multiple: true,
            action: uploadFile,
            data: file => ({
                // data
                // 
                uid: file.uid,
            }),
            onRemove: file => {
                const { uid ,response} = file;
                // response.code0
                if (response && 0 === response.code){
                    // 
                    _this.deleteFile({'uid':uid})
                }
            },
            onChange(info) {
                // uploading done error removed
                const { status ,response} = info.file;
                if (status === 'done' || status === 'error') {
                    if (0 === response.code){
                        openNotificationWithIcon("success", "", `${info.file.name} file uploaded successfully.`);
                        _this.getDatas();
                    }else{
                        openNotificationWithIcon("error", "", `${info.file.name} file upload failed.cause by:${response.msg}`);
                    }
                }
            },
        };
    };

    /*
     * Table
     */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'filename', // 
            },
            {
                title: '',
                dataIndex: 'source', // 
            },
            {
                title: '',
                render: (text, record) => {
                    if (record.status === '1') {
                        return ''
                    } else if (record.status === '2') {
                        return ''
                    } else {
                        return ''
                    }
                }
            },
            {
                title: '',
                dataIndex: 'date', // 
            },
            {
                title: '',
                render: (text, record) => (
                    <div>
                        <Button type="primary" onClick={() => this.downloadFile(record)} shape="circle" icon={<CloudDownloadOutlined/>}/>
                        &nbsp;
                        <Button type="primary" onClick={() => this.handleChangeFile(record)} shape="circle" icon={<EditOutlined/>}/>
                        &nbsp;
                        <Button type="danger" onClick={() => this.handleDeleteFile(record)} shape="circle" icon={<DeleteOutlined/>}/>
                    </div>
                ),
            },
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
            filename: this.state.filters.filename,
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getFileList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.filename = null;
        _this.setState({
            nowPage: 1,
            filters: filters
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * ,
     * @param pageSize
     * @param current
     */
    changePageSize = (pageSize, current) => {
        // reactevent handlersetState, state.
        this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        this.setState({
            nowPage: current,
        }, function () {
            this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== ''){
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        }else{
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nowPage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * 
     * @param event
     */
    fileInputInputChange = (event) => {
        let _this = this;
        const value = event.target.value;
        let filters = _this.state.filters;
        filters.filename = value;
        _this.setState({
            nowPage: 1,
            filters
        })
    };

    /**
     * 
     */
    handleCancelUpload = () => {
        let _this = this;
        let uploadVisible = false;
        _this.setState({
            uploadVisible
        })
    };

    /**
     * 
     */
    handleOpenUpload = () => {
        let _this = this;
        let uploadVisible = true;
        _this.setState({
            uploadVisible
        })
    };

    /**
     * 
     */
    deleteFile = async (para) => {
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code} = await deleteFile(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            openNotificationWithIcon("success", "", "");
            this.getDatas();
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     */
    handleDeleteFile = (item) => {
        let _this = this;
        Modal.confirm({
            title: '',
            content: `:'${item.filename}'?`,
            onOk: () => {
                let para = { id: item.id };
                _this.deleteFile(para)
            }
        })
    };

    /**
     * 
     * @param row
     */
    downloadFile = (row) => {
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        axios({
            method: "GET",
            url: downloadFileForAdmin+row.id,   //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                console.log(res)
                _this.setState({listLoading: false});
                let fileName = row.filename;//
                let blob = new Blob([res.data]);
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", ""+res);
            });
    };

    /**
     * 
     * @param item
     */
    handleChangeFile = (item) => {
        const _this = this;
        let message = '';
        let sendStatus = null;
        if (item.status === '1')
        {
            // 
            sendStatus = 2;
            message = `' ${item.filename} '`
        } else {
            // 
            sendStatus = 1;
            message = `' ${item.filename} '`
        }
        Modal.confirm({
            title: '',
            content: message,
            onOk: async () => {
                let para = { id: item.id, status: sendStatus };
                // , loading
                _this.setState({listLoading: true});
                // ajax, 
                const {msg, code} = await editFile(para);
                // , loading
                _this.setState({listLoading: false});
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.getDatas();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            }
        })

    };

    /*
     *render()
     * async
     */
  componentDidMount() {
      // 
      this.initUpload();
      // 
      this.initColumns();
      // 
      this.getDatas();
  };


    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters, uploadVisible} = this.state;
        let {beginTime,endTime} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null){
            rangeDate = [moment(beginTime),moment(endTime)]
        } else {
            rangeDate = [null,null]
        }
        return (
            <DocumentTitle title=''>
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Input type='text' value={filters.filename} onChange={this.fileInputInputChange}
                                       placeholder=''/>
                            </Form.Item>
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                  <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                  <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.handleOpenUpload}>
                                    <CloudUploadOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="id" bordered loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                    <Modal
                        title=""
                        visible={uploadVisible === true}
                        onOk={this.handleCancelUpload}
                        onCancel={this.handleCancelUpload}>
                        <Dragger {...this.uploadConfig}>
                            <p className="ant-upload-drag-icon">
                                <Icon type="inbox" />
                            </p>
                            <p className="ant-upload-text"></p>
                            <p className="ant-upload-hint">
                                10Mexe/bat
                            </p>
                        </Dragger>
                    </Modal>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default Files;
