import React, {Component} from 'react';
import {downloadBackUpDB, getBackUpDBList} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import {Button, Col, Form, DatePicker, Table} from "antd";
import moment from 'moment';
import axios from "axios";
import DocumentTitle from 'react-document-title'
import {ReloadOutlined, SearchOutlined,CloudDownloadOutlined} from "@ant-design/icons";
import {disabledDate} from "../../../utils/var";
/*
 * index.jsx
 * liunengkai
 * 2019-07-09 - 22:44
 * 
 */
const {RangePicker} = DatePicker;

// ES6
class DB extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            beginTime: null,// 
            endTime: null,// 
        },
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'archiveDate', // 
            },
            {
                title: '',
                dataIndex: 'url', // 
            },
            {
                title: '',
                dataIndex: 'createtime', // 
            },
            {
                title: '',
                render: (text, record) => (
                    <span onClick={() => this.downloadFile(record)}>
                        <CloudDownloadOutlined/>
                    </span>
                ),
            },
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getBackUpDBList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    reloadPage = () => {
        // 
        const _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        _this.setState({
            nowPage: 1,
            filters: filters
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        // reactevent handlersetState, state.
        this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        this.setState({
            nowPage: current,
        }, function () {
            this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        const _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== ''){
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        }else{
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nowPage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };

    downloadFile = (row) => {
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        axios({
            method: "GET",
            url: downloadBackUpDB+"?archiveDate="+row.archiveDate,   //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = row.archiveDate+'.sql';//
                let blob = new Blob([res.data]);
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", ""+res);
            });
    };


    /*
    *render()
    * async
    */
  componentDidMount() {
      // 
      this.initColumns();
      // 
      this.getDatas();
  };

    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters} = this.state;
        let {beginTime,endTime} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null){
            rangeDate = [moment(beginTime),moment(endTime)]
        } else {
            rangeDate = [null,null]
        }
        return (
            <DocumentTitle title=''>
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                  <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                  <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" bordered rowKey="url" loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default DB;
