import React, {Component} from 'react';
import {Form, Input,Modal} from "antd";
import {clearTrimValueEvent} from "../../../utils/string";
import {createMemo, updateMemo} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
/*
 * edit.jsx
 * shmily
 * 2019-08-25 - 21:50
 * 
 */

// ES6
class EditMemo extends Component {

    formRef = React.createRef();

    state = {
        memo: {}, // 
        visibleModal:false
    };

    /**
     * 
     */
    handleCancel = () => {
        this.setState({visibleModal: false});
    };

    /**
     * 
     * @param val
     */
    handleDisplay = (val) => {
        let _this = this;
        console.log('val',val);
        _this.setState({
            memo: val,
            visibleModal: true
        },function () {
            // initialValues  setState  setFieldsValue 
            if(!val || !val.id){
                _this.formRef.current.setFieldsValue({'title':null, 'content':null});
            }else{
                _this.formRef.current.setFieldsValue(val);
            }
        });
    };

    /**
     * 
     */
    handleSubmit = () => {
        const _this = this;
        const memo = _this.state.memo;
        _this.formRef.current.validateFields(['title', 'content']).then(value => {
            if(!memo.id){
                // 
                _this.handleAddMemo(value);
            }else{
                // 
                value.id = memo.id;
                _this.handleRenewMemo(value);
            }
        }).catch(e => console.log("",e));
    };

    /**
     * 
     * @param value
     * @returns {boolean}
     */
    handleAddMemo = (value) => {
        const _this = this;
        Modal.confirm({
            title: '?',
            onOk: async () => {
                const {msg, code} = await createMemo(value);
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.props.refreshList();
                    _this.handleCancel();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            },
            onCancel() {
                return false;
            },
        });
    };

    handleRenewMemo = (value) => {
        const _this = this;
        Modal.confirm({
            title: '?',
            onOk: async () => {
                const {msg, code} = await updateMemo(value);
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.props.refreshList();
                    _this.handleCancel();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            },
            onCancel() {
                return false;
            },
        });
    };


    /*
     * render()
     * async
     */
    componentDidMount() {
        this.formItemLayout = {
            labelCol: {span: 4},
            wrapperCol: {span: 14},
        };
        // 
        const _this = this;
        _this.props.onRef(_this);
    };


    render() {
        const {memo,visibleModal} = this.state;
        return (
            <Modal title={!memo || !memo.id ? '':''} visible={visibleModal} maskClosable={false} okText='' onOk={this.handleSubmit} onCancel={this.handleCancel}>
                <Form {...this.formItemLayout} ref={this.formRef}>
                    <Form.Item label="" {...this.formItemLayout} initialValue={memo.title} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='title' rules={[{required: true, message: ''}, {min: 2, message: ' 2  15 '}, {max: 15, message: ' 2  15 '}]}>
                        <Input placeholder=''/>
                    </Form.Item>
                    <Form.Item label="" {...this.formItemLayout} initialValue={memo.content} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='content' rules={[{required: true, message: ''}, {max: 128, message: ' 1  128 '}]}>
                        <Input.TextArea showCount placeholder='' maxLength={128} autosize={{minRows: 4, maxRows: 6}}/>
                    </Form.Item>
                </Form>
            </Modal>
        );
    }
}

// 
export default EditMemo;
