import React, {Component} from 'react';
import {Button, Form, Col, Input, Tag, Select} from "antd";
import RichTextEditor from '../../../component/for-editor'
import {openNotificationWithIcon} from "../../../utils/window";
import {getNotes, updateNotes, createNotes, getNoteBookGroup} from "../../../api/index"
import DocumentTitle from 'react-document-title'
import "./index.less"
import {clearTrimValueEvent} from "../../../utils/string";
import {PlusOutlined} from "@ant-design/icons";
/*
 * edit.jsx
 * liunengkai
 * 2019-07-27 - 21:44
 * 
 */
const {Option} = Select;
// ES6
class EditNote extends Component {

    // ref
    formRef = React.createRef();
    markdownRef = React.createRef();

    state = {
        tagColor:['magenta','red','volcano','orange','gold','lime','green','cyan','blue','geekblue','purple'],
        tags: [],
        inputVisible: false,
        inputValue: '',
        form: {},
        // id   
        isUpdate: false,
        id: '',
        notebooks: []
    };

    /**
     * tag
     * @param removedTag
     */
    handleClose = removedTag => {
        const tags = this.state.tags.filter(tag => tag !== removedTag);
        this.setState({ tags });
    };

    /**
     * 
     */
    showInput = () => {
        let _this = this;
        let tags = _this.state.tags;
        // 5tag
        if (tags.length < 5){
            this.setState({ inputVisible: true }, () => this.input.focus());
        }
    };

    handleInputChange = e => {
        this.setState({ inputValue: e.target.value });
    };

    /**
     * tag
     */
    handleInputConfirm = () => {
        const { inputValue } = this.state;
        let { tags } = this.state;
        if (inputValue && tags.indexOf(inputValue) === -1) {
            tags = [...tags, inputValue];
        }
        this.setState({
            tags,
            inputVisible: false,
            inputValue: '',
        });
    };


    forMap = tag => {
        let colors = this.state.tagColor;
        const tagElem = (
            <Tag
                closable
                color={colors[Math.floor(Math.random()*10)]}
                onClose={e => {
                    e.preventDefault();
                    this.handleClose(tag);
                }}
            >
                {tag}
            </Tag>
        );
        return (<span key={tag} style={{ display: 'inline-block' }}>{tagElem}</span>
        );
    };


    /**
     * 
     */
    submit = () => {
      let _this = this;
      let {tags, isUpdate, id} = _this.state;
      // 
      if (_this.markdownRef.isNull() === true){
          openNotificationWithIcon("error", "", "");
          return
      }
      // 
      const editor = _this.markdownRef.getValue();
      _this.formRef.current.validateFields(['topic', 'notebookId']).then(value => {
        // 
        let _thisTag = null
        if (tags.length > 1){
            _thisTag = tags.join(';')
        } else if(tags.length === 1){
            _thisTag = tags[0]
        } else {
            _thisTag = null
        }
        let para = {
          topic: value.topic,
          label: _thisTag,//
          content: editor,
          notebookId: value.notebookId,
        };
        if (isUpdate){
            // 
            para.id = id
            _this.updateNotes(para)
        } else{
            // 
            _this.createNotes(para)
        }
      }).catch(e => console.log("",e));
    };

    // 
    updateNotes = async (param) => {
        const {msg, code} = await updateNotes(param);
        if (code === 0) {
            openNotificationWithIcon("success", "", "");
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    // 
    createNotes = async (param) => {
        const {msg, code} = await createNotes(param);
        if (code === 0) {
            openNotificationWithIcon("success", "", "");
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };


    /**
     * 
     * @returns {Promise<void>}
     */
    initDatas = async (id) => {
      const _this= this;
      const para = {
          id: id
      };
      // ajax, 
      const {msg, code, data} = await getNotes(para);
      if (code === 0) {
        _this.setState({
              tags:data.label === null ? [] : (data.label).split(';'),
              form: data,
              isUpdate:true,
              id: id
          },function () {
          _this.formRef.current.setFieldsValue(data);
          _this.markdownRef.initEditor(data.content);
        });
      } else {
          openNotificationWithIcon("error", "", msg);
      }
    };

    /**
     * 
     */
    getNoteBooks = async () =>{
        let _this = this;
        // ajax, 
        const {msg, code, data} = await getNoteBookGroup()
        if (code === 0) {
            let notebooks = [];
            data.forEach(item => {
                notebooks.push((<Option key={item.id} value={item.id}>{item.name}</Option>));
            });
            _this.setState({
                notebooks
            })
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    }

    /**
     * markdownref
     * @param ref
     */
    bindMarkDownRef = (ref) => {
      this.markdownRef = ref
    };

    /**
     * 
     */
    componentDidMount() {
        let _this = this;
        // 
        _this.getNoteBooks()
        // state
        let notes = _this.props.location.state;
        // , 
        if (notes){
            _this.initDatas(notes.id)
        } else {
          let form = {content:' '};// 
          this.setState({
              isUpdate:false,
              form: form
          },function () {
            _this.formRef.current.setFieldsValue({'topic':null, 'notebookId':null});
            _this.markdownRef.initEditor(' ');
          });
        }
        this.formItemLayout = {
            labelCol: {span: 2},
            wrapperCol: {span: 21},
        };
        this.buttonItemLayout = {
            wrapperCol: {span: 21, offset: 2},
        };
    };


    render() {
        //const {getFieldDecorator} = this.props.form;
        const { tags, inputVisible, inputValue,form,notebooks} = this.state;
        const tagChild = tags.map(this.forMap);
        return (
            <DocumentTitle title="">
                <section className="b-edit-notes-page">
                    <div className="mytips">
                        <p></p>
                        <blockquote>
                            <p>
                                <i>1</i>
                            </p>
                            <p>
                                <i>25(5)</i>
                            </p>
                            <p>
                                <i>3</i>
                            </p>
                            <p>
                                <i>4MarkDown</i>
                            </p>
                        </blockquote>
                    </div>
                    <Col span={24} className="b-edit-notes-page-form">
                        <Form {...this.formItemLayout} ref={this.formRef} className="bk-transparent">
                          <Form.Item {...this.buttonItemLayout} style={{'textAlign':'right'}}>
                            <Button htmlType="button" type="primary" onClick={this.submit}></Button>
                          </Form.Item>
                            <Form.Item label="" {...this.formItemLayout} name='topic' initialValue={form.topic} getValueFromEvent={ (e) => clearTrimValueEvent(e)} rules={[ {required: true, message: ''}, {max: 50, message: '50'},]}>
                              <Input type='text' style={{width:'40em'}}/>
                            </Form.Item>
                            <Form.Item label="" {...this.formItemLayout} name='notebookId' initialValue={form.notebookId} rules={[{required: true, message: ''}]}>
                              <Select style={{width:'34em'}} showSearch
                                      placeholder="">
                                {notebooks}
                              </Select>
                            </Form.Item>
                            <Form.Item label="" {...this.formItemLayout}>
                                <div style={{ marginBottom: 16 }}>
                                    {tagChild}
                                </div>
                                {inputVisible && (
                                    <Input
                                        ref={input => this.input = input}
                                        type="text"
                                        size="small"
                                        style={{ width: 78 }}
                                        value={inputValue}
                                        onChange={this.handleInputChange}
                                        onBlur={this.handleInputConfirm}
                                        onPressEnter={this.handleInputConfirm}
                                    />
                                )}
                                {!inputVisible && (
                                    <Tag onClick={this.showInput} >
                                      <PlusOutlined/> New Tag
                                    </Tag>
                                )}
                            </Form.Item>
                            <Form.Item label="" {...this.formItemLayout}>
                                <RichTextEditor onRef={this.bindMarkDownRef.bind(this)}/>
                            </Form.Item>
                        </Form>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

