import React, {Component} from 'react';
import Cropper from 'react-cropper'// Cropper
import 'cropperjs/dist/cropper.css'//
import {Modal} from 'antd';
import './uploadLogo.less';
import {uploadLogo} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import memoryUtils from "../../../utils/memoryUtils";
/*
 * uploadLogo.jsx
 * liunengkai
 * 2019-07-14 - 22:04
 * 
 * 1https://www.jianshu.com/p/eeaa9fba56d0
 * 2https://blog.csdn.net/qq_37043418/article/details/109238963
 */

// ES6
class UploadLogo extends Component {

  state = {
      modalVisible: false,//
      confirmLoading: false,//load
      src: null,//
      headerImage: '',//
  };

  onCropperInit = cropper => { this.cropper = cropper }

  componentDidMount() {
      const fileReader = new FileReader();
      fileReader.onload = (e) => {
          const dataURL = e.target.result;
          this.setState({src: dataURL})
      };
      // logo
      const user = memoryUtils.user;
      const headerImage = user.logo;
      this.setState({
          headerImage
      });
      let file = this.props.uploadedImageFile;
      if (file) {
          fileReader.readAsDataURL(this.props.uploadedImageFile)
      }
  }

  //
  handleFileChange = (e) => {
      const file = e.target.files[0];
      if (file) {
          //5M
          if (file.size / 1024 <= 5 * 1024) {
              const reader = new FileReader();
              reader.onload = () => {
                  this.setState({
                      src: reader.result
                  }, () => {
                      this.setState({
                          modalVisible: true,
                      })
                  })
              };
              reader.readAsDataURL(file);

          } else {
              openNotificationWithIcon("error", "", "5M");
          }
      } else {
          openNotificationWithIcon("error", "", "");
      }
      e.target.value = ''
  };

    //
    saveCropperImg = () => {
      let _this = this;
        // 
        if (_this.cropper.getCroppedCanvas() === 'null') {
            return false
        }
        // this.setState({
        //     confirmLoading: true,
        // });
        // Crop
        const croppedCanvas = _this.cropper.getCroppedCanvas();
        // Round
        var roundedCanvas = _this.getRoundedCanvas(croppedCanvas);
        //Canvasbase64
        let headerImage = roundedCanvas.toDataURL();
        _this.setState({
            headerImage
        }, function () {
            // 
            _this.upload(headerImage)
        })
    };

    upload = async (image) => {
        let _this = this;
        _this.setState({
            confirmLoading: true,
        });
        //
        let para = {
            'logo': image.toString()
        };
        const result = await uploadLogo(para);
        _this.setState({
            confirmLoading: false,
        });
        let {msg, code} = result;
        if (code === 0) {
            openNotificationWithIcon("success", "", "");
            _this.setState({
                modalVisible: false,
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    // 
    getRoundedCanvas = (sourceCanvas) => {
        var canvas = document.createElement('canvas');
        var context = canvas.getContext('2d');
        var width = sourceCanvas.width;
        var height = sourceCanvas.height;
        canvas.width = width;
        canvas.height = height;
        context.imageSmoothingEnabled = true;
        context.drawImage(sourceCanvas, 0, 0, width, height);
        context.globalCompositeOperation = 'destination-in';
        context.beginPath();
        context.arc(width / 2, height / 2, Math.min(width, height) / 2, 0, 2 * Math.PI, true);
        context.fill();
        return canvas;
    };

    //
    cancelCropper = () => {
        this.setState({
            modalVisible: false,
        });
    };


    render() {
        // 
        const {headerImage, modalVisible, confirmLoading, src} = this.state;
        return (
            <div className='upload-cropper'>
                {/*  */}
                <div className="show">
                    <div className="picture" style={{backgroundImage: 'url(' + headerImage + ')'}}></div>
                </div>
                <div className="upload-box">
                    <span className="upload-span" role="button">
                      <input
                          type="file"
                          accept="image/*"
                          className="base-upload-input"
                          onChange={this.handleFileChange}
                      />
                      <div className="button">
                        <div></div>
                      </div>
                    </span>
                </div>
                {/* modal */}
                <Modal
                    title=""
                    width="70vw"
                    closable={false}
                    destroyOnClose={true}
                    visible={modalVisible}
                    onOk={this.saveCropperImg}
                    confirmLoading={confirmLoading}
                    onCancel={this.cancelCropper}>
                    <div className="cropperModal">
                        <Cropper
                            src={src}//base64Upload
                            style={{height: 400, width: '100%'}}
                            preview='.cropper-preview'
                            className="company-cropper"
                            viewMode={1} //cropper
                            zoomable={true} //
                            aspectRatio={this.props.aspectRatio} //image1:1
                            guides={false} //
                            background={false} //
                            rotatable={true} //
                            //ref='cropper'//ref
                            //crop={this._crop.bind(this)}
                            onInitialized={this.onCropperInit.bind(this)}
                        />
                        <div className='preview-button'>
                            <div className="cropper-preview" style={{
                                borderRadius: '50%',
                                height: 100,
                                width: '100%'
                            }}></div>
                        </div>

                    </div>

                </Modal>

            </div>
        )
    }

}

// 
export default UploadLogo;
