import React, { Component } from 'react';
import {Row, Col, Table, Modal} from 'antd';
import {getTransactionDetail} from "../../../api";
import {openNotificationWithIcon,showLoading} from "../../../utils/window";
import {formatMoney} from '../../../utils/var'
import moment from 'moment';
/*
 * detail.jsx
 * saya
 * 2021/1/3 - 1:59
 * 
 */

/**
 * css
 */
const detailHeader = {
  'height': '5em',
  'display': 'flex',
  'alignItems': 'center',
  'justifyItems': 'center',
  'textAlign': 'center',
  'fontSize': '1.4em'
};

const detailTradeNumber = {
  'display': 'flex',
  'alignItems': 'center',
  'justifyItems': 'left',
  'textAlign': 'left',
  'fontSize': '1.1em'
};

const detailTradeDate = {
  'height': '4em',
  'display': 'flex',
  'alignItems': 'center',
  'justifyItems': 'left',
  'textAlign': 'left',
  'fontSize': '1.1em',
};

const inputLabel = {
  'fontWeight': 'bold'
};

const tableFooter = {
  'fontSize':'0.4em'
};

const detailGrid = {
  'paddingBottom': '0.5em'
};

// ES6
class BillDetail extends Component {

  state = {
    // 
    bill: null,
    // 
    listLoading: false,
    tradeId: -1,
    visibleModal:false
  };

  /**
  * Table
  */
  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'id', // 
        align:'center',
      },
      {
        title: '',
        render:() => (!this.state.bill?'-':this.state.bill.source),
        align:'center',
      },
      {
        title: '',
        align:'center',
        render: (text, record) => {
          if (record.flog === 1) {
            return ''
          } else if (record.flog === 2) {
            return ''
          } else {
            return ''
          }
        }
      },
      {
        title: '',
        dataIndex: 'currencyDetails', // 
      },
      {
        title: '',
        dataIndex: 'currencyNumber', // 
        align:'right',
        render:(value,row) => (formatMoney(row.currencyNumber))
      }
    ]
  };

  /**
   * 
   * @returns {Promise<void>}
   */
  getDatas = async () => {
    let para = {
      tradeId: this.state.tradeId
    };
    // , loading
    this.setState({listLoading: showLoading()});
    // ajax, 
    const {msg, code, data} = await getTransactionDetail(para);
    // , loading
    this.setState({listLoading: false});
    if (code === 0) {
      this.setState({
        bill: data
      });
    } else {
      openNotificationWithIcon("error", "", msg);
    }
  };

  handleCancel = () => {
    this.setState({visibleModal: false});
  };

  handlePrint = () => {
    window.document.body.innerHTML = window.document.getElementById('billDetails').innerHTML;
    window.print();
    window.location.reload();
  };

  handleDisplay = (val) => {
    let _this = this;
    _this.setState({
      tradeId: val,
      visibleModal: true
    },function () {
      // 
      _this.getDatas()
    });
  };


  /**
   * 
   */
  componentDidMount() {
    // 
    this.initColumns();
    // 
    const _this = this;
    _this.props.onRef(_this);
    _this.setState({bill:null})
  };

  render() {
    const {tradeId, bill, listLoading,visibleModal} = this.state;
    return (
        <Modal
            title=''
            width="80%"
            visible={visibleModal}
            onOk={() => this.handlePrint()}
            onCancel={() => this.handleCancel()}
            cancelText=''
            okText=''>
          <section className="transaction-detail" id='billDetails'>
            <Row style={detailHeader}>
              <Col span={12} offset={6}>
                {!bill||!bill.tradeDate?'-':moment(bill.tradeDate).format('YYYYMMDD')}
              </Col>
            </Row>
            <Row style={detailTradeNumber}>
              <Col span={5} offset={19}>
                <span style={inputLabel}></span>{tradeId}
              </Col>
            </Row>
            <Row style={detailTradeDate}>
              <Col span={5} offset={19}>
                <span style={inputLabel}></span>{!bill?'-':moment(bill.tradeDate).format('YYYYMMDD')}
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col span={8}>
                <div><span style={inputLabel}></span>{formatMoney(!bill?0:bill.currencyNumber)}</div>
              </Col>
              <Col span={8}>
                <div><span style={inputLabel}></span>{formatMoney(!bill?0:bill.deposited)}</div>
              </Col>
              <Col span={8}>
                <div><span style={inputLabel}></span>{formatMoney(!bill?0:bill.expenditure)}</div>
              </Col>
              <Col span={8}>
                <div><span style={inputLabel}></span>{!bill||!bill.tradeTypeEntity?'-':bill.tradeTypeEntity.transactionType}</div>
              </Col>
              <Col span={8}>
                <div><span style={inputLabel}></span>{!bill||!bill.tradeAmountEntity?'-':bill.tradeAmountEntity.tag}</div>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Table size="middle" style={detailGrid} rowKey="id" bordered pagination={false} loading={listLoading} columns={this.columns} dataSource={!bill?null:bill.infoList}/>
              </Col>
            </Row>
            <Row>
              <Col style={tableFooter} span={6}>
                <div><span style={inputLabel}></span>{!bill?'-':bill.source}</div>
              </Col>
              <Col style={tableFooter} span={6}>
                <div><span style={inputLabel}></span>{!bill||!bill.createTime?'-':moment(bill.createTime).format('YYYYMMDD HH:mm:ss')}</div>
              </Col>
              <Col style={tableFooter} span={6}>
                <div><span style={inputLabel}></span>{!bill||!bill.updateTime?'-':moment(bill.updateTime).format('YYYYMMDD HH:mm:ss')}</div>
              </Col>
              <Col style={tableFooter} span={6}>
                <div><span style={inputLabel}></span>{moment().format('YYYYMMDD HH:mm:ss')}</div>
              </Col>
            </Row>
          </section>
        </Modal>
    );
  }
}

// 
export default BillDetail;
