import React, {Component} from 'react';
import {Form, Input, Modal, Radio, Tooltip} from "antd";
import {clearTrimValueEvent} from "../../../utils/string";
import {QuestionCircleOutlined} from "@ant-design/icons";
import {openNotificationWithIcon} from "../../../utils/window";
import {createNoteBook, updateNoteBook} from "../../../api";
/*
 * edit.jsx
 * liunengkai
 * 2019-08-25 - 21:50
 * 
 */

// ES6
class EditNoteBook extends Component {

  formRef = React.createRef();

  state = {
    book: {}, // 
    visibleModal: false
  };

  /**
   * 
   */
  handleCancel = () => {
    this.setState({visibleModal: false});
  };

  /**
   * 
   * @param val
   */
  handleDisplay = (val) => {
    let _this = this;
    _this.setState({
      book: val,
      visibleModal: true
    }, function () {
      // initialValues  setState  setFieldsValue 
      if (!val || !val.id) {
        _this.formRef.current.setFieldsValue({'name': null, 'descript': null,'status':1});
      } else {
        _this.formRef.current.setFieldsValue(val);
      }
    });
  };

  /**
   * 
   */
  handleSubmit = () => {
    const _this = this;
    const book = _this.state.book;
    _this.formRef.current.validateFields(['name', 'descript','status']).then(value => {
      if(!book.id){
        // 
        _this.handleAddBook(value);
      }else{
        // 
        value.id = book.id;
        _this.handleRenewBook(value);
      }
      console.log('val',value)
    }).catch(e => console.log("",e));
  }

  /**
   * 
   * @param value
   * @returns {boolean}
   */
  handleAddBook = (value) => {
    const _this = this;
    Modal.confirm({
        title: '?',
        onOk: async () => {
          const {msg, code} = await createNoteBook(value);
          if (code === 0) {
            openNotificationWithIcon("success", "", "");
            _this.props.refreshList();
            _this.handleCancel();
          } else {
            openNotificationWithIcon("error", "", msg);
          }
        },
        onCancel() {
            return false;
        },
    });
  }

  /**
   * 
   * @param value
   * @returns {boolean}
   */
  handleRenewBook = (value) => {
    const _this = this;
    Modal.confirm({
        title: '?',
        onOk: async () => {
          const {msg, code} = await updateNoteBook(value);
          if (code === 0) {
            openNotificationWithIcon("success", "", "");
            _this.props.refreshList();
            _this.handleCancel();
          } else {
            openNotificationWithIcon("error", "", msg);
          }
        },
        onCancel() {
            return false;
        },
    });
  }

  /*
   * render()
   * async
   */
  componentDidMount() {
    this.formItemLayout = {
      labelCol: {span: 6},
      wrapperCol: {span: 14},
    };
    // 
    const _this = this;
    _this.props.onRef(_this);
  };

  render() {
    const {book,visibleModal} = this.state;
    return (
      <Modal title={!book || !book.id ? '':''} visible={visibleModal} maskClosable={false} okText='' onOk={this.handleSubmit} onCancel={this.handleCancel}>
        <Form {...this.formItemLayout} ref={this.formRef}>
          <Form.Item label="" {...this.formItemLayout} initialValue={book.name} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='name' rules={[{required: true, message: ''},{min: 2, message: ' 2  15 '},{max: 15, message: ' 2  15 '}]}>
            <Input placeholder=''/>
          </Form.Item>
          <Form.Item label="" {...this.formItemLayout} initialValue={book.descript} getValueFromEvent={ (e) => clearTrimValueEvent(e)} name='descript' rules={[{required: true, message: ''}, {min: 1, message: ' 1  50 '},{max: 50, message: ' 1  50 '}]}>
            <Input.TextArea autosize={{minRows: 2, maxRows: 4}} showCount maxLength={50} placeholder=''/>
          </Form.Item>
          <Form.Item label={<span>&nbsp;<Tooltip title=""><QuestionCircleOutlined /></Tooltip></span>} {...this.formItemLayout} initialValue={book.status || 1} name='status' rules={[{required: true, message: ''}]}>
            <Radio.Group>
              <Radio value={1}></Radio>
              <Radio value={2}></Radio>
            </Radio.Group>
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

// 
export default EditNoteBook;
