import React, {Component} from 'react';
import {totalTransactionForDay, outTransactionForDayExcel} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import moment from 'moment';
import axios from 'axios';
import {SearchOutlined, ReloadOutlined, FileExcelOutlined} from '@ant-design/icons';
import DocumentTitle from 'react-document-title'
import {Button, Col, DatePicker, Table, Form} from "antd";
import {disabledDate, formatMoney} from "../../../utils/var";
/*
 * index.jsx
 * liunengkai
 * 2019-09-01 - 15:09
 * 
 */
const {RangePicker} = DatePicker;
// ES6
class FinancialForDay extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            beginTime: null,// 
            endTime: null,// 
        },
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'tradeDate', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'deposited', // 
                align:'right',
                render:(value,row) => (formatMoney(row.deposited))
            },
            {
                title: '',
                dataIndex: 'expenditure', // 
                align:'right',
                render:(value,row) => (formatMoney(row.expenditure))
            },
            {
                title: '',
                dataIndex: 'currencyNumber', // 
                align:'right',
                render:(value,row) => (formatMoney(row.currencyNumber))
            }
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let _this = this;
        let para = {
            beginTime: _this.state.filters.beginTime,
            endTime: _this.state.filters.endTime,
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
        };
        // , loading
        _this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await totalTransactionForDay(para);
        // , loading
        _this.setState({listLoading: false});
        if (code === 0) {
            _this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        _this.setState({
            nowPage: 1,
            filters: filters
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        let _this = this;
        // reactevent handlersetState, state.
        _this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            _this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        let _this = this;
        _this.setState({
            nowPage: current,
        }, function () {
            _this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== '') {
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        } else {
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nowPage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * 
     */
    exportExcel = () =>{
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        let para = {
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        axios({
            method: "GET",
            url: outTransactionForDayExcel,   //
            params: para,           //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = '.xlsx';//excel
                let blob = new Blob([res.data], {type: 'application/x-xlsx'});   //wordmsword,pdfpdfexcelx-xls
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                console.log(res);
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", "");
            });
    };

    /*
    *render()
    * async
    */
    componentDidMount() {
        // 
        this.initColumns();
        // 
        this.getDatas();
    };


    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading, filters} = this.state;
        let {beginTime, endTime} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null) {
            rangeDate = [moment(beginTime), moment(endTime)]
        } else {
            rangeDate = [null, null]
        }
        return (
            <DocumentTitle title="">
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item>
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                    <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                    <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.exportExcel}>
                                    <FileExcelOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" bordered rowKey='tradeDate' loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default FinancialForDay;