import React, {Component} from 'react';
import {Col, Form, Button, Table, DatePicker, Select} from 'antd';
import {getLogList, getLogType, downloadLogExcel} from '../../../api'
import {openNotificationWithIcon} from '../../../utils/window'
import {SearchOutlined,ReloadOutlined,FileExcelOutlined} from '@ant-design/icons';
import moment from 'moment';
import axios from 'axios'
import DocumentTitle from 'react-document-title'
import './index.less'
import {disabledDate} from "../../../utils/var"
/*
 * index.jsx
 * liunengkai
 * 2019-07-09 - 22:31
 * 
 */
const {RangePicker} = DatePicker;
const {Option} = Select;

// ES6
class Logs extends Component {

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            // 
            date: null,
            beginTime: null,// 
            endTime: null,// 
            selectType: ''//
        },
        type: [],// 
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'user', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'logType.describe', // 
                render: (text, record) => <span>{!record.logType?'-':record.logType.describe}</span>,
            },
            {
                title: 'IP',
                dataIndex: 'ip', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'city', // 
            },
            {
                title: '',
                dataIndex: 'date', // 
                align:'center',
            }
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getTypeData = async () => {
        let _this = this;
        // ajax, 
        const {msg, code, data} = await getLogType();
        if (code === 0) {
            // 
            let type = [];
            type.push((<Option key={-1} value=""></Option>));
            data.forEach(item => {
                type.push((<Option key={item.type} value={item.type}>{item.describe}</Option>));
            });
            _this.setState({
                type
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let _this = this;
        let para = {
            nowPage: _this.state.nowPage,
            pageSize: _this.state.pageSize,
            type: _this.state.filters.selectType,
            beginTime: _this.state.filters.beginTime,
            endTime: _this.state.filters.endTime,
        };
        // , loading
        _this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getLogList(para);
        // , loading
        _this.setState({listLoading: false});
        if (code === 0) {
            _this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.selectType = '';
        _this.setState({
            nowPage: 1,
            filters: filters
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        let _this = this;
        // reactevent handlersetState, state.
        _this.setState({
            pageSize: pageSize,
            nowPage: 1,
        }, function () {
            _this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        let _this = this;
        _this.setState({
            nowPage: current,
        }, function () {
            _this.getDatas();
        });
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== ''){
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        }else{
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            filters,
            nowPage: 1,
        }, function () {
            _this.getDatas()
        });
    };

    // 
    onChangeType = (value) => {
        let _this = this;
        let {filters} = _this.state;
        filters.selectType = value;
        _this.setState({
            filters,
            nowPage: 1,
        }, function () {
            _this.getDatas()
        });
    };

    exportExcel = () => {
        let _this = this;
        // , loading
        _this.setState({listLoading: true});
        let para = {
            type: this.state.filters.selectType,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
        };
        axios({
            method: "GET",
            url: downloadLogExcel,   //
            params: para,           //
            responseType: 'blob',
            //{responseType: 'arraybuffer'}
            headers: {
                "Content-Type": "application/json"
            },
        })
            .then(function (res) {
                _this.setState({listLoading: false});
                let fileName = '.xlsx';//excel
                let blob = new Blob([res.data], {type: 'application/x-xlsx'});   //wordmsword,pdfpdfexcelx-xls
                if (window.navigator.msSaveOrOpenBlob) {
                    navigator.msSaveBlob(blob, fileName);
                } else {
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.download = fileName;
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
            })
            .catch(function (res) {
                _this.setState({listLoading: false});
                openNotificationWithIcon("error", "", "");
            });
    };


    /*
    *render()
    * async
    */
  componentDidMount() {
      // 
      this.getTypeData();
      // 
      this.initColumns();
      // 
      this.getDatas();
  };

    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters, type} = this.state;
        let {beginTime,endTime} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null){
            rangeDate = [moment(beginTime),moment(endTime)]
        } else {
            rangeDate = [null,null]
        }
        return (
            <DocumentTitle title=''>
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Select value={filters.selectType} className="queur-type" showSearch onChange={this.onChangeType}
                                        placeholder="">
                                    {type}
                                </Select>
                            </Form.Item>
                            <Form.Item label=":">
                                <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                    <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                    <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.exportExcel}>
                                    <FileExcelOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="id" bordered loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default Logs;
