import React, {Component} from 'react';
import {Button, Col, Tag, Input, Table, Form, Modal} from "antd";
import {deleteNoteBook, getNoteBookList} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import DocumentTitle from 'react-document-title'
import {DeleteOutlined, EditOutlined, PlusOutlined, ReloadOutlined, SearchOutlined} from "@ant-design/icons";
import EditNoteBook from "./edit";

/*
 * index.jsx
 * liunengkai
 * 2019-08-24 - 21:58
 * 
 */

// ES6
class NoteBook extends Component {

  bookFormRef = React.createRef();

    state = {
        // 
        datas: [],
        // 
        dataTotal: 0,
        // 
        nowPage: 1,
        // 
        pageSize: 10,
        // 
        listLoading: false,
        filters: {
            name: null,// 
            status: null// 
        },
    };

    /*
    * Table
    */
    initColumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'name', // 
                align:'center',
            },
            {
                title: '',
                align:'center',
                render: (text, record) => {
                    if (record.status === 1) {
                        return <Tag color="success"></Tag>
                    } else if (record.status === 2) {
                        return <Tag color="error"></Tag>
                    } else {
                        return <Tag color="warning"></Tag>
                    }
                }
            },
            {
                title: '',
                dataIndex: 'source', // 
                align:'center',
            },
            {
                title: '',
                dataIndex: 'notesCount', // 
                align:'right',
            },
            {
                title: '',
                align:'center',
                render: (text, record) => (
                    <div>
                        <Button type="primary" onClick={() => this.handleModalEdit(record)} shape="circle" icon={<EditOutlined/>}/>
                        &nbsp;
                        <Button type="danger"  onClick={() => this.handleDeleteNoteBook(record)} shape="circle" icon={<DeleteOutlined/>}/>
                    </div>
                ),
            },
        ]
    };

    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            name: this.state.filters.name,
            nowPage: this.state.nowPage,
            pageSize: this.state.pageSize,
            status: this.state.filters.status
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getNoteBookList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            this.setState({
                // 
                dataTotal: data.dateSum,
                // 
                datas: data.grid
            });
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.name = null;
        filters.status = null;
        _this.setState({
            nowPage: 1,
            filters: filters,
        }, function () {
            _this.getDatas()
        });
    };

    // ,
    changePageSize = (pageSize, current) => {
        // reactevent handlersetState, state.
        this.setState({
            nowPage: 1,
            pageSize: pageSize
        }, function () {
            this.getDatas();
        });
    };

    // 
    changePage = (current) => {
        this.setState({
            nowPage: current,
        }, function () {
            this.getDatas();
        });
    };

    /*
     * 
     */
    handleModalAdd = () => {
      this.bookFormRef.handleDisplay({});
    };

    /*
    * 
    */
    handleModalEdit = (value) => {
      this.bookFormRef.handleDisplay(value);
    };

    bindBookFormRef = (ref) => {
      this.bookFormRef = ref
    };

    refreshListFromBookForm= () =>{
      this.getDatas();
    };

    /*
    * 
    */
    handleDeleteNoteBook = (item) => {
        let _this = this;
        let tips = '';
        if (item.notesCount > 0){
            tips = `${item.name}${item.notesCount}`
        }else{
            tips = `${item.name}`
        }
        Modal.confirm({
            title: '',
            content: tips,
            cancelText: '',
            okText: '',
            onOk: async () => {
                // , loading
                _this.setState({listLoading: true});
                let para = { id: item.id };
                const {msg, code} = await deleteNoteBook(para);
                // , loading
                _this.setState({listLoading: false});
                if (code === 0) {
                    openNotificationWithIcon("success", "", "");
                    _this.getDatas();
                } else {
                    openNotificationWithIcon("error", "", msg);
                }
            }
        })
    };

    /**
     * 
     * @param event
     */
    nameInputChange = (event) => {
        let _this = this;
        const value = event.target.value;
        let filters = _this.state.filters;
        filters.name = value;
        _this.setState({
            nowPage: 1,
            filters
        })
    };

    /*
     * render()
     * async
     */
    componentDidMount() {
      // 
      this.initColumns();
      this.refreshListFromBookForm  = this.refreshListFromBookForm.bind(this);
      // 
      this.getDatas();
    };


    render() {
        // 
        const {datas, dataTotal, nowPage, pageSize, listLoading,filters} = this.state;
        return (
            <DocumentTitle title="">
                <section>
                    <Col span={24} className="toolbar">
                        <Form layout="inline">
                            <Form.Item label=":">
                                <Input type='text' value={filters.name} onChange={this.nameInputChange}
                                       placeholder=''/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                  <SearchOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                  <ReloadOutlined/>
                                </Button>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="button" onClick={this.handleModalAdd}>
                                  <PlusOutlined/>
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={24} className="dataTable">
                        <Table size="middle" rowKey="id" bordered loading={listLoading} columns={this.columns} dataSource={datas}
                               pagination={{
                                   current:nowPage,
                                   showTotal: () => `${nowPage} ${dataTotal}`,
                                   pageSize: pageSize, showQuickJumper: true, total: dataTotal, showSizeChanger: true,
                                   onShowSizeChange: (current, pageSize) => this.changePageSize(pageSize, current),
                                   onChange: this.changePage,
                               }}/>
                    </Col>
                  <EditNoteBook onRef={this.bindBookFormRef.bind(this)} refreshList={this.refreshListFromBookForm}/>
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default NoteBook;
