import React, {Component} from 'react';
import {Button, Row, Col, Input, Form, DatePicker, Modal, Spin, Upload} from "antd";
import moment from 'moment';
import './index.less'
import {getPictureList, deletePicture, uploadWallpaper, setUserInfo} from "../../../api";
import {openNotificationWithIcon} from "../../../utils/window";
import DocumentTitle from 'react-document-title'
import axios from 'axios'
import memoryUtils from "../../../utils/memoryUtils";
import storageUtils from "../../../utils/storageUtils";
import {ReloadOutlined, SearchOutlined,CloudUploadOutlined,DeleteOutlined,HeartOutlined,MinusOutlined,MoreOutlined,CheckOutlined} from "@ant-design/icons";
import {disabledDate,isEmptyObject} from "../../../utils/var";

/*
 * index.jsx
 * liunengkai
 * 2019-08-17 - 20:40
 * 
 */
const {RangePicker} = DatePicker;

// ES6
class Wallpaper extends Component {

    state = {
        filters: {
            beginTime: null,// 
            endTime: null,// 
            filename: null
        },
        // 
        datas: [],
        // 
        nextpage: 1,
        // 
        listLoading: false,
        // 
        pageSize: 10,
        // 
        uploadVisible: false,
        previewVisible: false,
        previewImage: '',
        fileList: [],
    };


    /**
     * 
     * @returns {Promise<void>}
     */
    getDatas = async () => {
        let para = {
            type: 1,
            nowPage: 'null' === this.state.nextpage ? 1 : this.state.nextpage,
            pageSize: this.state.pageSize,
            beginTime: this.state.filters.beginTime,
            endTime: this.state.filters.endTime,
            filename: this.state.filters.filename,
        };
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code, data} = await getPictureList(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            // 
            this.rendering(data);
        } else {
            this.setState({nextpage: 'null'});
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     * @param data
     */
    rendering = (data) => {
        let {datas, nextpage} = this.state;
        // 
        if (!(isEmptyObject(data.grid))) {
            //
            if (data.pageNow === 1) {
                datas = data.grid;//Vue
            } else {
                datas = (datas).concat(data.grid);//
            }
        } else {
            datas = 'null';
        }
        //()
        //console.log("",data.pageNow === data.totalPage)
        if (data.pageNow === data.totalPage) {
            nextpage = 'null';
        } else {
            nextpage = data.pageNow + 1;
        }
        //console.log("",nextpage)
        this.setState({
            datas: datas,
            nextpage: nextpage
        })
    };

    // 
    onChangeDate = (date, dateString) => {
        let _this = this;
        let {filters} = _this.state;
        // 
        if (dateString[0] !== '' && dateString[1] !== '') {
            filters.beginTime = dateString[0];
            filters.endTime = dateString[1];
        } else {
            filters.beginTime = null;
            filters.endTime = null;
        }
        _this.setState({
            nextpage: 1,
            filters
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * 
     */
    reloadPage = () => {
        // 
        let _this = this;
        let filters = _this.state.filters;
        filters.beginTime = null;
        filters.endTime = null;
        filters.filename = null;
        _this.setState({
            nextpage: 1,
            filters: filters
        }, function () {
            _this.getDatas()
        });
    };

    /**
     * 
     * @param event
     */
    fileInputInputChange = (event) => {
        let _this = this;
        const value = event.target.value;
        let filters = _this.state.filters;
        filters.filename = value;
        _this.setState({
            nextpage: 1,
            filters
        })
    };

    /**
     * 
     */
    handleDeleteFile = (e) => {
        let _this = this;
        // 
        let id =  e.currentTarget.getAttribute('data-id')
        Modal.confirm({
            title: '',
            content: `:'${id}'?`,
            onOk: () => {
                let para = { id: id};
                _this.deletePicture(para)
            }
        })
    };

    /**
     * 
     * @param e
     */
    handleSetBackbround = async (e) => {
        let _this = this;
        // 
        let src =  e.currentTarget.getAttribute('data-src')
        let id =  e.currentTarget.getAttribute('data-id')
        let flag = false;
        // axios
        await axios.get(src)
            .then(function(response){
                flag = true
            })
            .catch(function(err){
                flag = false
            });
        if (true === flag) {
            let para = {
                background: parseInt(id)
            };
            // , loading
            _this.setState({listLoading: true});
            const result = await setUserInfo(para);
            // , loading
            _this.setState({listLoading: false});
            let {msg, code} = result;
            if (code === 0) {
                const data = memoryUtils.user;
                data.user.background = src;
                data.user.backgroundId = parseInt(id);
                memoryUtils.user = data;// 
                storageUtils.saveUser(data); // local
                openNotificationWithIcon("success", "", "");
            } else {
                openNotificationWithIcon("error", "", msg);
            }
        }else{
            openNotificationWithIcon("error", "", '');
        }
    };

    /**
     * 
     * @param para
     * @returns {Promise<void>}
     */
    deletePicture = async (para) => {
        // , loading
        this.setState({listLoading: true});
        // ajax, 
        const {msg, code} = await deletePicture(para);
        // , loading
        this.setState({listLoading: false});
        if (code === 0) {
            openNotificationWithIcon("success", "", "");
            this.getDatas();
        } else {
            openNotificationWithIcon("error", "", msg);
        }
    };

    /**
     * 
     */
    handleCancelUpload = () => {
        let _this = this;
        let uploadVisible = false;
        _this.setState({
            uploadVisible:uploadVisible,
            fileList: []
        })
    };

    /**
     * 
     */
    handleOpenUpload = () => {
        let _this = this;
        let uploadVisible = true;
        _this.setState({
            uploadVisible
        })
    };

    /**
     * Base64
     * @param file
     * @returns {Promise<any>}
     */
    getBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
        });
    };

    /**
     * 
     */
    handleCancel = () => this.setState({ previewVisible: false });

    /**
     * 
     * @param file
     * @returns {Promise<void>}
     */
    handlePreview = async (file) => {
        // if (!file.url && !file.preview) {
        //     file.preview = this.getBase64(file.originFileObj);
        // }
        this.setState({
            previewImage: file.url || file.thumbUrl,
            previewVisible: true,
        });
    };

    /**
     * list
     * @param fileList
     */
    handleChange = (info) => {
        let fileList = info.fileList;
        const { status } = info.file;
        // statusdoneuploadingdong
        if (status !== 'uploading') {
            console.log(info.file, info.fileList);
        }
        if (status === 'done') {
            openNotificationWithIcon("success", "", `${info.file.name} file uploaded successfully.`);
            this.getDatas();
        } else if (status === 'error') {
            openNotificationWithIcon("error", "", `${info.file.name} file upload failed.`);
        }
        this.setState({ fileList });
    };

    // handleChange = ({ fileList }) => {
    //     this.setState({ fileList });
    // };

    /**
     * 
     * @param file
     */
    handleDelete = (file) => {
        //console.log(file)
        this.setState(state => {
            const index = state.fileList.indexOf(file);
            //console.log(index)
            const newFileList = state.fileList.slice();
            newFileList.splice(index, 1);
            return {
                fileList: newFileList,
            };
        },function () {
            console.log(this.state.fileList)
        });
    };

    /**
     * 
     */
    previewPhoto = () => {
        console.log("")
    };

    /*
    : ajax
     */
    componentDidMount() {
        // 
        this.getDatas();
    };


    render() {
        // 
        const {filters, datas, nextpage, listLoading, uploadVisible, previewVisible, previewImage, fileList} = this.state;
        let {beginTime, endTime} = filters;
        let rangeDate;
        if (beginTime !== null && endTime !== null) {
            rangeDate = [moment(beginTime), moment(endTime)]
        } else {
            rangeDate = [null, null]
        }
        const user = memoryUtils.user;
        const uploadButton = (
            <div>
                <CloudUploadOutlined/>
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        return (
            <DocumentTitle title=''>
                <section>
                    <Modal
                        title=""
                        visible={uploadVisible === true}
                        onOk={this.handleCancelUpload}
                        onCancel={this.handleCancelUpload}>
                        <Upload
                            action={uploadWallpaper}
                            listType="picture-card"
                            accept="image/jpeg,image/jpg,image/png,image/bmp"
                            fileList={fileList}
                            onPreview={this.handlePreview}
                            onChange={this.handleChange}
                            onRemove={this.handleDelete}>
                            {fileList.length >= 8 ? null : uploadButton}
                        </Upload>
                    </Modal>
                    <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                        <img alt="example" style={{ width: '100%' }} src={previewImage} />
                    </Modal>
                    <Row>
                        <Col span={24} className="toolbar">
                            <Form layout="inline">
                                <Form.Item label=":">
                                    <Input type='text' value={filters.filename} onChange={this.fileInputInputChange}
                                           placeholder=''/>
                                </Form.Item>
                                <Form.Item label=":">
                                    <RangePicker value={rangeDate} disabledDate={disabledDate} onChange={this.onChangeDate}/>
                                </Form.Item>
                                <Form.Item>
                                    <Button type="primary" htmlType="button" onClick={this.getDatas}>
                                      <SearchOutlined/>
                                    </Button>
                                </Form.Item>
                                <Form.Item>
                                    <Button type="primary" htmlType="button" onClick={this.reloadPage}>
                                      <ReloadOutlined/>
                                    </Button>
                                </Form.Item>
                                <Form.Item>
                                    <Button type="primary" htmlType="button" onClick={this.handleOpenUpload}>
                                        <CloudUploadOutlined/>
                                    </Button>
                                </Form.Item>
                            </Form>
                        </Col>
                    </Row>
                    {
                        listLoading === true ? <Spin/> :
                            <ul className="wallpaper-ul">
                                {datas !=='null' ? datas.map((item) => (
                                        <li span={6} className="album-div-imgdiv" key={item.id}>
                                            <div className="tools">
                                                <Button type="primary" shape="circle" icon={<DeleteOutlined/>} data-id={item.id} onClick={this.handleDeleteFile} size="small" title=""/>
                                                {
                                                    user.user.backgroundId === item.id ?
                                                        null
                                                    :
                                                        <Button type="primary" style={{marginLeft: '0.5em'}} shape="circle" icon={<HeartOutlined />} data-id={item.id} data-src={item.weburl} onClick={this.handleSetBackbround} size="small" title=""/>
                                                }
                                            </div>
                                            <a href="#toolbar" onClick={this.previewPhoto} rel="noopener noreferrer" className="a-img">
                                                <img src={item.weburl} alt={item.filename}
                                                     className="img-responsive"/>
                                            </a>
                                        </li>
                                    )):
                                    <li span={6} className="album-div-imgdiv">
                                        <Button type="primary" shape="circle" icon={<MinusOutlined/>} size="small" title=""/>
                                    </li>
                                }
                                {nextpage !=='null' ?
                                    <li span={6} className="album-div-imgdiv">
                                        <Button type="primary" onClick={this.getDatas} shape="circle" icon={<MoreOutlined/>} size="small" title=""/>
                                    </li>
                                    :
                                    <li span={6} className="album-div-imgdiv">
                                        <Button type="primary" shape="circle" icon={<CheckOutlined/>} size="small" title=""/>
                                    </li>
                                }
                            </ul>
                    }
                </section>
            </DocumentTitle>
        );
    }
}

// 
export default Wallpaper;
